body {
  margin: 0;
  padding: 0;
  font-family: 'Questrial', sans-serif;
}

h1 {
  margin-left: 100px;
  font-family: 'Abril Fatface', cursive;
  color: #F75133;
  font-size: 40px;
}

h2 {
  color: #F7F7F7;
  background-color: #FBAF37;
  font-family: 'Abril Fatface', cursive;
  padding: 10px 0 10px 20px;
  font-size: 26px;
}

h4 {
  margin-left: 100px;
  color: #66483F;
  font-size: 22px;
}

h5 {
  font-family: 'Questrial', sans-serif;
}
.home-div {
    margin-top: 30px;
}

.dashboard {
    width: 70%;
    margin: auto;
    margin-top: 4%;
    box-shadow:0 0 8px #605856;
}

.home-buttons {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    background-color: white;
    padding: 10px;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
}

.home-button {
    height: 225px;
    width: 225px;
    margin: 25px;
    box-shadow:0 0 8px #605856;
    font-size: 30px;
    color: white;
    font-family: 'Questrial', sans-serif;
}

.home-button:hover {
    cursor: pointer;
    -webkit-transform: scale(1.05);
            transform: scale(1.05)
}

.homelink:hover {
    color: white;
    text-decoration: none;
}


#button1 {
    border-color: #FBAF37;
    background-color: #FBAF37;
}

#button2 {
    border-color: #F75133;
    background-color: #F75133;
}

#button3 {
    border-color: #66483F;
    background-color: #66483F;
}

#button4 {
    border-color: #50A3A4;
    background-color: #50A3A4;
}
ul {
    background-color: #FBAF37;
    font-size: 16px;
    height: 60px;
    line-height: 2.5
}

a {
    color: #F7f7f7;
    height: 100%;
}

a:hover {
    color: #DC851F;
}

.nav-link:hover {
    border: 0px;
}

.logout-btn {
    width: 10%;
    float: right;
    border-color: #50A3A4;
    background-color: #50A3A4;
    color: white;
    height: 35px;
    border-radius: 125px;
    margin-top: .75%;
    margin-left: 2%;
    line-height: 2.1;
    box-shadow: 0 0 3px #605856;
}

.logout-btn:hover {
    cursor: pointer;
    color: #9BC1BC;
}

.collection-div {
    margin-top: 30px;
}

.collection-items-div {
    margin: 30px auto 30px auto;
    width: 80%;
    box-shadow:0 0 8px #605856;
    font-family: 'Abril Fatface', cursive;
    padding-bottom: 10px;
    background-color: white;
}

.categories {
    list-style-type: none;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    margin: 0;
    padding: 0;
    line-height: 3.0;
    color: #66483F;
    background-color: white;
    font-family: 'Questrial', sans-serif;
}

.each-card{
    margin: 0.8%;
}

.item-card-div {
    height: 100%;
    display: -webkit-flex;
    display: flex;
    padding: 1% 2%;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    -webkit-align-content: flex-start;
            align-content: flex-start;
    overflow: auto;
}

.img-container {
    height: 50%;
}

.img-container > img {
    margin: 1%;
    max-width: 95%;
    height: 250px;
  }

.card {
    border-radius: 2px;
    position: relative;
    width: 250px;
    text-align: left;
    font-family: 'Questrial', sans-serif;
    margin-bottom: 15px;
    border-color: white;
}

.card:hover {
    border-color: #C8D3D5;
}

.card-title {
    color: #F75133;
    padding-right: 20px;
    font-size: 18px;
    line-height: 1
}

.card-text {
    color: #605856;
    max-height: 20px;
    margin: 10px auto ;
}

.card-body {
    max-height: 200px;
}

.line-2 {
    margin-top: 25px;
    margin-bottom: 25px;
}

.btn {
    color: white;
    background-color: #50A3A4;
    border-color: #50A3A4;
    height: 40px;
    padding-top: 7px;
    border-radius: 50px;
    width: 100%;
    box-shadow: 0 0 3px #605856;
    font-family: 'Questrial', sans-serif;
}

.btn:hover {
    color: #9BC1BC;
}

.under-construction {
    width: 85%;
    margin: 3% auto;
}

.under-construction > img {
    max-width: 100%
}
.post-item-div {
    margin-top: 30px;
}

.form-div {
    margin: 35px auto 30px auto;
    width: 55%;
    box-shadow:0 0 8px #605856;
    padding-bottom: 1px;
    background-color: white;
}

form {
    margin: 5% 0 5% 6%;
    color: #F75133;
    font-family: 'Questrial', sans-serif; 
}

#post-submit-btn {
    margin-top: 2%;
    height: 45px;
    width: 100%;
    border-radius: 125px;
    color: white;
}

#map {
    height: 100%;
    width: 100%;
}

#man {
    height: 100%;
    width: 100%;
}

.form-head {
    height: 100%;
    padding: 10px 0 2px 15px;
}

.form-control {
    border: solid 1px #E7E6F7;
}
.requests-page-div {
    margin-top: 30px;
}

.requests-div {
    margin: 30px auto 30px auto;
    width: 66%;
    height: 100%;
    box-shadow: 0 0 8px #605856;
    background-color: white;
}

.request-card {
    border: solid 1px white;
    height: 75px;
    margin: 1% 1%;
    padding: 1% 1%;
}

.request-card:hover {
    border-color: #C8D3D5;
}

.request-title {
    color: #F75133;
    font-size: 20px;
}

.request-description {
    color: #66483F;
}

.middle-col {
    line-height: 20px;
}

.middle-col-item {
    color: red;
    margin-right: 10px;
}

.request-item-img > img {
    height: 60%;
    width: 60%;
}

.request-btn {
    color: #f7f7f7;
    background-color: #50A3A4;
    border-color: #50A3A4;
    border-radius: 50px;
    height: 66%;
    width: 90%;
}

.request-link:hover {
    text-decoration: none;
    color: #9BC1BC;
}

.delete-request {
    margin-top: 2.75%;
}
.head {
  background-color: #FBAF37;
  box-shadow: 0 0 8px #605856;
}

.login-inputs {
  height: 100px;
  line-height: 0px;
}

.appName {
  margin-top: 20px;
  font-size: 60px;
  color: white;
}

.login-btn {
  color: white;
  height: 55%;
  margin-top: 10px;
  width: 50%;
  padding-top: 4px;
  box-shadow: 0 0 3px #605856;
}

.login-body{
  margin: 5% 8% 0% 8%;
}

h3 {
  font-size: 35px;
  margin-left: 0;
  color: #F75133;
}

.welcome-head {
  margin-left: 15%;
}

.welcome-msg {
  font-size: 20px;
  margin: 6% 15% 0% 15%;
  line-height: 2.3;
  color: #66483F;
}

.bold {
  font-weight: bolder;
  color: #F75133;
}

.new {
  font-size: 25px;
  margin-left: 29%;
  margin-top:8%;
}

.signup-head {
  font-size: 40px;
  color: #50A3A4;
  width: 100%;
  margin-right: 15%;
}

.footer-img {
  width: 100%;
  position: absolute;
  bottom: 0;
  z-index: -1;
}

.gotosignup {
  width: 30%;
  margin-left: 29%;
  height: 45px;
  font-size: 18px;
  box-shadow: 0 0 3px #605856;
}

.signuplink:hover {
  text-decoration: none;
  color: #9BC1BC;
}

.signup-div {
  margin-top: 4.5%;
  z-index: 1;
  background-color: white;
  width: 50%;
}

.signup-form{
  margin: 10% 5%;
}

.signupbtn {
  color: white;
  width: 100%;
  margin-top: 9%;
  height: 50px;
  box-shadow: 0 0 3px #605856;
}

.right-side {
  width: 90%;
  margin: 4% 7.5%
}

.pic {
  height: 100%;
  width: 100%;
}

.lines {
  border-top: none;
  border-left: none;
  border-right: none;
}

.to-login-content {
 margin-left: 60%;
 padding-top: 3%;
 color: #605856;
}

.back-to-login:hover {
  text-decoration: none;
}
